import React from 'react';
import { useNavigate } from 'react-router-dom';
import PageWrapper from '../../../layout/PageWrapper/PageWrapper';
import Page from '../../../layout/Page/Page';
import Button from '../../../components/bootstrap/Button';
import { globalPages } from '../../../menu';
import Header from '../../../components/Header';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../components/bootstrap/Dropdown';
import Dropdown from '../../../components/bootstrap/Dropdown';

const Page404 = () => {

	const navigate = useNavigate()


	const alterarLocalidade = () => {
		localStorage.removeItem('CAPP-local');
		navigate('/')
	};

	return (
		<PageWrapper title={globalPages.page404.text}>
				<Header	titulo='App CC'	localidade='Não encontrado'>
				<Dropdown >
				<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
						<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Página Inicial
						</DropdownItem>
					
					</DropdownMenu>
				</Dropdown>
		</Header>
			<Page>
				<div className='row d-flex align-items-center h-50'>
					<center>
						<br /><br />
						<h1 style={{"fontSize":"40px", "color":"lightgray"}}>Página não encontrada!</h1>
						<br />
						</center>
				</div>
			</Page>
		</PageWrapper>
	);
};

export default Page404;
